import { Result } from "antd";
import React, { useMemo } from "react";
import { useNavigate } from "react-router-dom";
import { AUTH_ROUTES } from "../../contants/RoutesEnum";
import Button from "../components/Buttons/Button";
import { useLocation } from "react-router-dom";
import { useTranslation } from 'react-i18next';
import { routeFileSelector } from '../../utilz/i18n';

const PageNotFound = () => {
  const navigate = useNavigate();
  const { pathname: activeTab} = useLocation();
  const namespace = useMemo(() => routeFileSelector(activeTab), [activeTab]);
  const { t } = useTranslation([namespace, 'translation']);

  return (
    <Result
      status="404"
      title="404"
      subTitle={t('translation:default.fields.thePageYouVisitedDoesNotExist')}
      extra={
        <Button
          onClick={() => navigate(AUTH_ROUTES.LOGIN, { replace: true })}
          type="primary"
        >
          {t('translation:default.fields.backHome')}
        </Button>
      }
    />
  );
};

export default PageNotFound;
